.thumb-grid {
	.make-row(@grid-gutter-width);
	
	.thumb {
		.make-sm-column (3, 0);
		height: 400px;
		margin-bottom: @line-height-computed;
		text-transform: uppercase;
		text-align: center;
		font-size: 1.0833em;
		line-height: 18px;
		letter-spacing: 1px;
	}	
	
	.thumb-desc {
		margin-top: 4px;
	}
	
	.thumb-desc-title {
		font-weight: 700;
	}		

	.thumb-desc-brand {
		color: #888;
	}
	.img-200x300 {
		.center-block();
	}
}

.package-grid {
   .make-row(@grid-gutter-width);
  
    .package {
		.make-sm-column (4, 0);
		margin-bottom: (2 * @line-height-computed);
		text-align: center;
		font-size: 1.0833em;
	}
    .package-name {
		text-transform: uppercase;
		text-align: center;
		letter-spacing: 1px;
        font-weight: 500;
	}	

    .package-desc {
        position: relative;
        height:  (@line-height-computed * 2);
        font-family: @component-font-family;
        line-height: 16px;
        color: @gray-light;
        width: 250px;
        text-align: center;
        overflow: hidden;
        .center-block();


        .package-desc-shadow {
            position: absolute;
            bottom: 0;
            .size(100%, @line-height-computed);
            #gradient > .vertical(@start-color: transparent; @end-color: #fff);
        }
    }
}

.img-200x300 {
    .size(200px, 300px);
}
.img-400x400 {
    .size(400px, 400px);
}