
.page-header {
	margin-bottom: 40px;
	padding: 4px 0;
	.clearfix();
	border-bottom: 1px solid #eee;

	.page-name {
		font-size: @font-size-h3;
		line-height: 1;
	}

	.pagination, 
	.page-name {
		margin: 0;
	}

	.pagination li a {
		font-size: 14px;
		font-family: @component-font-family;
		line-height: 1;
		min-width: 32px;
		text-align: center;
	}
}
.container > .page-header {
	margin-left: (@grid-gutter-width / 2);
}