.footer:extend(.container all) {    	
	.footer-row:extend(.center-block) {
        padding-bottom: (@line-height-computed * 3);
	    padding-top: (@line-height-computed * 3);
	}	
	.footer-list-col:extend(.center-block) {
		max-width: 66%;
	}
	.footer-list-heading { 
        text-align: center;
        font-size: 20px;
	}
	.footer-list:extend(.list-unstyled) {
        li:extend(.text-center) { 
			padding-bottom: (@line-height-computed / 2); 
		}		
		&, a {
			color: @gray-light;
			font-size: 18px;
		}
	}
}