.header:extend(.container all) {

    .logo {
        height: 102px;
    }

    .top-brand {
        padding-top: 8px;
        position: relative;
        text-align: center;
    }

    .contact-options {
        position: absolute;
        top: 36px;
        right: (@grid-gutter-width / 2);
        font-size: @font-size-medium;
        font-family: @component-font-family;

        .contact-label {
            width: 48px;
            display: inline-block;
        }
    }

    .navbar.affix {
        top: 0;
        left: 0;
        width: 100%;
        z-index: @zindex-navbar;
        // chrome fix
        -webkit-backface-visibility: hidden;
        -webkit-transform: translateZ(0);
    }

    .navbar {
        font-weight: 700;
        .social-link {
            .opacity(0.3);

            &:hover {
                .opacity(1);
            }
        }
    }
}

.top-alert {
    text-align: center;
    padding: 10px 0;
    background-color: lighten(@brand-primary, 20%);
    color: #fff;
}

.contact-label {
    margin-right: 10px;
}