
.featurette {    
	.feature-box p {
	    max-width: 90%;
		font-size: 1.75em;
		color: @gray-light;	
	}
    .single-heading {
        margin: 0;
        text-align: center;
    }
}

.featurette-heading {
    padding-top: 60px;
    padding-bottom: 60px;
}

.featurette-sided {
	
    .feature-content-row {
        .make-row();
    }

	.feature-content-left {
		.make-sm-column(6);	
	}

	.feature-content-right {
		.make-sm-column(6);	
	}
}

.featurette-sided {
	.feature-content-right .feature-box {
		padding-top: 60px;
        padding-left: 60px;
    }
	.feature-content-left .feature-img {
        .size(100%, 100%);
        margin-left: -(@grid-gutter-width / 2);
        background-size: cover;
    }
}

.featurette-brand {
    color: #fff;
    background: @brand-bg;
}

.featurette-satin {
    background: @gray-lighter;
}